.slider-btn {
  --size: 54px;
  background: #f3f3f3;
  transition: 0.4s;
}
.slider-btn.swiper-button-disabled {
  opacity: 0;
}
.slider-btn.transparent {
  background: transparent;
  border: 1px solid #949698;
}
.slider-btn.transparent svg path {
  stroke: white;
}
.slider-btn svg {
  width: 16px;
  height: 16px;
}
.slider-btn::after {
  display: none;
}

@media (max-width: 1024px) {
  .pc {
    display: none;
  }
}

.tablet {
  display: none;
}
@media (max-width: 1024px) {
  .tablet {
    display: block;
  }
}
@media (max-width: 568px) {
  .tablet {
    display: none;
  }
}

.mobile {
  display: none;
}
@media (max-width: 1024px) {
  .mobile {
    display: none;
  }
}
@media (max-width: 568px) {
  .mobile {
    display: block;
  }
}

.app-checkbox {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 10px;
  color: #202929;
}
.app-checkbox input {
  display: none;
}
.app-checkbox div {
  width: 19px;
  height: 19px;
  flex: 0 0 19px;
  border-radius: 3px;
  border: 1px solid #949698;
  position: relative;
  transition: 0.4s;
}
.app-checkbox div svg {
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
  opacity: 0;
  width: 20px;
  height: 20px;
  transition: 0.4s;
}
.app-checkbox div svg path {
  stroke: white;
}
.app-checkbox:has(input:checked) div {
  background-color: #202929;
  border-color: #202929;
}
.app-checkbox:has(input:checked) svg {
  opacity: 1;
}

.app-radio {
  display: flex;
  align-items: center;
  gap: 8px;
}
.app-radio input {
  display: none;
}
.app-radio > .circle {
  --size: 16px;
  border: 1px solid #949698;
  transition: 0.4s;
  padding: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.app-radio > .circle .circle {
  transition: 0.4s;
  --size: 10px;
  background-color: #202929;
  opacity: 0;
}
.app-radio:has(input:checked) > .circle {
  border-color: #202929;
}
.app-radio:has(input:checked) > .circle .circle {
  opacity: 1;
}

.app-select {
  position: relative;
  z-index: 2;
}
.app-select.open .select__wrapper {
  background-color: white;
  border-color: #949698;
}
.app-select.open .select__content {
  opacity: 1;
  pointer-events: all;
}
.app-select.open .select__back {
  pointer-events: all;
  opacity: 1;
}
.app-select .select {
  width: 100%;
}
.app-select .select__wrapper {
  width: fit-content;
}
.app-select .select__back {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100dvh;
  opacity: 0;
  pointer-events: none;
}
.app-select .select__title {
  width: fit-content;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 16px;
  gap: 8px;
  transition: 0.4s;
}
.app-select .select__title svg path {
  stroke: #202929;
}
.app-select .select__content {
  width: 168px;
  opacity: 0;
  pointer-events: none;
  transition: 0.4s;
  border-radius: 5px;
  background: var(--white, #fff);
  /* drop down */
  box-shadow: 0px 4px 30px 0px rgba(0, 0, 0, 0.03);
  position: absolute;
  top: 29px;
  left: 0;
  padding: 24px 20px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.app-select .select__content > button {
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  border-radius: 100px;
  border: 1px solid #949698;
  /* 1440/Body 6 medium */
  font-family: "TT Drugs";
  font-size: 14px;
  font-weight: 500;
  line-height: 120%; /* 16.8px */
}
.app-select .select__list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.app-select .select__item {
  cursor: pointer;
  /* 1440/Body 7 regular */
  font-family: "TT Drugs";
  font-size: 14px;
  font-weight: 400;
  line-height: 130%; /* 18.2px */
  color: #202929;
  transition: 0.4s;
}
.app-select .select__item.selected {
  color: #949698;
}

.app-tabs {
  display: flex;
  gap: 60px;
  margin-left: calc(-1 * var(--containerFull));
  margin-right: calc(-1 * var(--containerFull));
  padding-left: calc(1 * var(--containerFull));
  padding-right: calc(1 * var(--containerFull));
  position: relative;
  column-gap: 60px;
  row-gap: 16px;
  flex-wrap: wrap;
}
@media (max-width: 1024px) {
  .app-tabs {
    column-gap: 24px;
  }
}
.app-tabs::after {
  content: "";
  display: block;
  background: #d4d4d4;
  height: 1px;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
}
.app-tabs input {
  display: none;
}
.app-tabs label {
  /* 1440/H6 medium CAPS */
  font-family: "TT Drugs";
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  color: #949698;
  transition: 0.4s;
  padding-bottom: 16px;
  display: flex;
  position: relative;
  z-index: 2;
}
.app-tabs label::after {
  opacity: 0;
  content: "";
  position: absolute;
  bottom: 0;
  left: -12px;
  width: calc(100% + 24px);
  height: 1px;
  background-color: #202929;
  transition: 0.4s;
}
@media (max-width: 1024px) {
  .app-tabs label::after {
    display: none;
  }
}
.app-tabs label:has(input:checked) {
  color: #202929;
}
.app-tabs label:has(input:checked)::after {
  opacity: 1;
}
@media (max-width: 1024px) {
  .app-tabs label {
    /* 768/H5 medium CAPS */
    font-family: "TT Drugs";
    font-size: 13px;
    font-weight: 500;
    text-transform: uppercase;
  }
}
